@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&family=Noto+Sans&display=swap);
:root {
  /* Meui-Light color definitions */
  --meui-light-background-color: #ffffff;
  --meui-light-secondary-background-color: #f5f5f5;

  --meui-light-text-color: #171a20;
  --meui-light-text-color-lowopacity: #171a2019;

  --meui-light-disabled-text-color: #888888;

  /* Meui-Dark color definitions */
  --meui-dark-background-color: #252525;
  --meui-dark-secondary-background-color: #151515;

  --meui-dark-text-color: #ffffff;
  --meui-dark-text-color-lowopacity: #ffffff19;

  --meui-dark-disabled-text-color: #5c5c5c;

  /* Other Meui color definitions */
  --meui-blue-accent-color: #2273e6;

  --meui-text-link-color: #2196f3;
}

h1,
h2 {
  font-weight: normal;
}

body {
  margin: 0;
  font-family: "Noto Sans";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  background-color: var(--meui-light-background-color);
  color: #171a20;
  color: var(--meui-light-text-color);
  overflow-x: hidden;
}

body.dark-mode {
  background-color: #252525;
  background-color: var(--meui-dark-background-color);
  color: #ffffff;
  color: var(--meui-dark-text-color);
}

code {
  font-family: "JetBrains Mono";
}

.website-content {
  margin: 0em 2em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.button-container {
  margin: 1em 0em;
}

.button {
  color: #171a20;
  color: var(--meui-light-text-color);
  text-decoration: none;
  font-size: 1.5em;
  border-color: #2273e6;
  border-color: var(--meui-blue-accent-color);
  border-width: 2px;
  border-style: solid;
  border-radius: 0.5em;
  padding: 0.3em 0.6em;
  margin: 0.5em;
  transition: background-color 0.125s ease-in-out;
  display: inline-block;
}

.dark-mode .button {
  color: #ffffff;
  color: var(--meui-dark-text-color);
  background-color: #151515;
  background-color: var(--meui-dark-secondary-background-color);
}

.button:hover {
  background-color: #171a2019;
  background-color: var(--meui-light-text-color-lowopacity);
}

.dark-mode .button:hover {
  background-color: #ffffff19;
  background-color: var(--meui-dark-text-color-lowopacity);
}

.disabled {
  color: #888888;
  color: var(--meui-light-disabled-text-color);
}

.dark-mode .disabled {
  color: #888888;
  color: var(--meui-light-disabled-text-color);
}

a {
  color: #2196f3;
  color: var(--meui-text-link-color);
}

.preview-image-container {
  width: 100%;
  max-width: 60rem;
  padding-bottom: 1em;
}

.preview-image {
  width: 100%;
  height: 100%;
  border-radius: 0.5em;
}

.website-header {
  height: 5em;
  width: 100vw;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  background-color: var(--meui-light-secondary-background-color);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: -webkit-sticky;
  position: sticky;
  z-index: 2;
}

.dark-mode .website-header {
  background-color: var(--meui-dark-secondary-background-color);
}

.website-header-text a {
  color: var(--meui-light-text-color);
  text-decoration: none;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: 2em;
}

.dark-mode .website-header-text a {
  color: var(--meui-dark-text-color);
}

.header-cyber-logo {
  width: 1.75em;
  height: 1.75em;
  margin-right: 0.5em;
}

.header-button {
  display: inline-block;
  margin: 0.5em;
}

a.header-button {
  text-decoration: none;
}

a.header-button:hover {
  text-decoration: underline;
}

.header-hamburger-button-container {
  display: block;
  position: absolute;
  top: 0em;
  right: 0;
  transition: ease-in-out 0.3s;
  transition-property: opacity, top;
  z-index: -1;
  background-color: var(--meui-light-secondary-background-color);
  opacity: 0;
  width: 100vw;
}

.dark-mode .header-hamburger-button-container {
  background-color: var(--meui-dark-secondary-background-color);
}

.header-hamburger-button-container.shown {
  display: block;
  top: 5em;
  opacity: 1;
}

.header-button-container {
  display: inline-block;
}

.header-button-hamburger {
  display: inline-block;
  border: none;
  background-color: transparent;
  width: 4em;
  height: 4em;
  outline: none;
}

.header-button-hamburger img {
  padding-top: 0.5em;
  width: 2.5em;
  height: 2.5em;
}

.header-hamburger-button-container .header-button {
  display: block;
  height: 0;
  transition: ease-in-out 0.3s height;
  padding-left: 1em;
}

.header-hamburger-button-container.shown .header-button {
  display: block;
  height: 2em;
}

.footer-container {
  display: -webkit-flex;
  display: flex;
  position: relative;
  left: 0;
  bottom: 0;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  color: var(--meui-light-text-color);
  text-align: center;
}

.dark-mode .footer-container {
  color: var(--meui-dark-text-color);
}

.footer-content p {
  margin: 0.5em;
}

.footer-copyright-notice {
  color: var(--meui-light-disabled-text-color);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.dark-mode .footer-copyright-notice {
  color: var(--meui-dark-disabled-text-color);
}

.about-container {
    max-width: 51rem;
    width: -webkit-fill-available;
}

